import { Component, OnInit } from '@angular/core';
import { MarkSixResult } from '../models/common.model';
import { CommonService } from '../services/common.service';
import { Mark6Service } from '../services/mark6.service';
import { LoadingController } from '@ionic/angular';


@Component({
  selector: 'app-result',
  templateUrl: './result.component.html',
  styleUrls: ['./result.component.scss']
})
export class ResultComponent implements OnInit {

  public firstTime = true;
  public loaded = false;
  public m6result: MarkSixResult;

  constructor(private loadingController: LoadingController, private commonService: CommonService,
    private m6service: Mark6Service) { }

  ngOnInit() {
    this.commonService.hideBannerAd();
    this.doRefresh();
  }

  ionViewDidEnter() {
    //this.commonService.prepareInterstitialAds();
    this.commonService.showInterstitialAds();
    this.commonService.hideBannerAd();
  }

  ionViewWillLeave() {
    this.commonService.showBannerAd();
  }


  async doRefresh() {
    if (this.firstTime === false) {
      this.commonService.showInterstitialAds();
    }

    this.firstTime = false;
    this.loaded = false;

    let loading = await this.loadingController.create({
      message: 'Loading',
      spinner: 'circles'
    });

    loading.present().then(() => {
      this.m6service.getResult().subscribe(result => {
        this.m6result = result.parameters.result;

        this.loaded = true;
        loading.dismiss();
      }, error => {
        loading.dismiss();
        this.loaded = false;
        console.log(error);
      });
    });
  }

  toDate(val): string {
    if (val != null) {
      return this.m6service.toDateStr(val);
    } else {
      return '';
    }
  }


  getNextDrawDate(m6result: MarkSixResult) {
    if (m6result.nextInfo != null && m6result.nextInfo.drawDate != null) {
      return this.toDate(m6result.nextInfo.drawDate);
    } else {
      return '';
    }
  }

  getNextDrawNo(m6result: MarkSixResult) {
    if (m6result.nextInfo != null && m6result.nextInfo.drawNo != null) {
      return m6result.nextInfo.drawNo;
    } else {
      return '';
    }
  }


  getNextJackpot(m6result: MarkSixResult) {
    if (m6result.nextInfo != null && m6result.nextInfo.jackpot != null) {
      return m6result.nextInfo.jackpot;
    } else {
      return '';
    }
  }


  shareToFacebook() {
    this.commonService.shareToFacebook();
  }
}
